//@import "product-onboarding-mixin";
@import "ScssHelpers/helpers-all.scss";

.product_onboarding__list {

	border-top: 1px solid $beige_2;
	@include bp($point_2, min) {
		padding-top: 4.6rem;
	}

	@include bp($point_3) {
		padding-top: 3.2rem;
	}
}

.product_onboarding__list_wrap {
	@include flex_block(row, wrap, flex-start, stretch, flex-start);
	@include bp($point_2, min) {
		margin: 0 -3rem -5.6rem 0;
	}

	@include bp($point_3) {
		margin: 0 -1.6rem -3rem 0;
	}
}

.product_onboarding__item {

	@include bp($point_2, min) {
		width: 25%;
		padding: 0 3rem 5.6rem 0;
	}

	@include bp($point_3, $point_4) {
		width: calc(100% / 3);
		padding: 0 1.6rem 3rem 0;
	}

	@include bp($point_4 - 1) {
		width: 50%;
		padding: 0 1.6rem 3rem 0;
	}
}
