@import "ScssHelpers/helpers-all.scss";

.product_onboarding {
}

.product_onboarding__link {
	display: block;
	overflow: hidden;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.4rem;
		}
	}

	&:focus,
	&:hover {
		text-decoration: none;

		.product_onboarding__img {
			scale: 1.06;
		}
	}
}

.product_onboarding__img {
	position: relative;
	display: block;
	width: 100%;
	height: 0;
	transition: scale 0.3s ease;

	@include bp($point_2, min) {
		padding-bottom: 98.8%;
	}

	@include bp($point_3) {
		padding-bottom: 100.8%;
	}
}

.product_onboarding__img_in {
	@include cover_img;
	@include stretch;
}

.product_onboarding__title {
	overflow: hidden;

	font-weight: 500;
	font-style: normal;
	letter-spacing: -0.016em;
	line-height: 1.16;

	color: $black;

	&:not(:last-child) {
		margin-bottom: .6rem;
	}

	a {
		color: inherit;
		transition: color 0.3s linear;

		&:hover,
		&:focus {
			text-decoration: none;
			color: $gold_2;
		}
	}

	@include bp($point_2, min) {
		min-height: 6.8rem;
		font-size: 2.8rem;
	}

	@include bp($point_3) {
		min-height: 4.2rem;
		font-size: 1.8rem;
	}
}

.product_onboarding__content {
	display: flex;
	flex-direction: column-reverse;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}
}

.product_onboarding__content_bottom {
	@include flex_block(row, nowrap, flex-start, center, center);
	color: $gold_2;
	cursor: pointer;
}

.product_onboarding__content_body {
	max-height: 0;
	opacity: 0;

	transition: all 0.25s ease-out;
	will-change: max-height;
	overflow: hidden;
	pointer-events: none;

	.product_onboarding__content--active_state & {
		opacity: 1;
		pointer-events: auto;
	}
}

.product_onboarding__content_spoiler {
	@include spoiler_acc();
	margin-right: 0.5rem;

	&:after {
		content: attr(data-close);
	}

	.product_onboarding__content--active_state & {
		&:after {
			content: attr(data-open);
		}
	}
}

.product_onboarding__content_icon {
	@include spoiler_acc_icon;

	.product_onboarding__content--active_state & {
		transform: rotate(-180deg);
	}
}

.product_onboarding__excerpt {
	margin-top: auto;

	color: $gray_1;
	font-weight: 500;
	letter-spacing: 0.07em;
	line-height: 1.13;
	text-transform: uppercase;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.2rem;
	}
}

.product_onboarding__rating {
	display: flex;
	align-items: center;

	color: $gold_1;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@include bp($point_3) {
		img {
			width: 41%;
		}
	}
}

.product_onboarding__description {

	@include bp($point_2, min) {
		padding-bottom: 1.2rem;
		line-height: 1.5;
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		padding-bottom: .6rem;
		font-size: 1.2rem;
		line-height: 1.64;
	}
}
